body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
}

.skills-container {
  background-image: url("../Assets/bg2.png");
  padding: 50px 20px;
  text-align: center;
  color: #fff;
}

.skills-heading {
  font-size: 3.5em;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
  animation: fadeInDown 1s ease-in-out;
}

.skills-box {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  margin-bottom: 30px;
  border-radius: 20px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  animation: fadeInUp 1s ease-in-out;
}

.skills-type {
  font-size: 2em;
  margin-bottom: 20px;
  color: #ffffff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
  animation: fadeInDown 1s ease-in-out;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); 
  gap: 20px;
}

.skills-card {
  animation: fadeInUp 1s ease-in-out;
  position: relative;
  overflow: hidden;
}

.skills-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4);
}

.skills-image {
  width: 40%; 
  border-radius: 15px;
  margin-bottom: 10px; 
  transition: transform 0.3s, opacity 0.3s;
}

.skills-card:hover .skills-image {
  transform: scale(1.05);
  opacity: 0.9;
}

.skills-title {
  color: #ffffff;
  font-size: 1.2em; 
  margin-top: 10px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .skills-heading {
    font-size: 3em;
  }

  .skills-type {
    font-size: 1.8em; 
  }

  .skills-card {
    padding: 10px;
  }
}

@media (max-width: 480px) {
  .skills-heading {
    font-size: 2.5em;
  }

  .skills-type {
    font-size: 1.5em;
  }

  .skills-card {
    padding: 8px;
  }
}
