.Form{
    background-image: url("../Assets/bg2.png");
}
.contact-heading {
    text-align: center;
    animation: fadeIn 1s ease; 
}

.contact-heading h1 {
    font-size: 3rem;
    color: #fff;
    margin-bottom: 10px;
    opacity: 0; 
    animation: slideIn 1s ease forwards;
}

.contact-heading p {
    font-size: 1.5rem;
    color: #fff;
    animation: slideIn 1s ease forwards 0.5s forwards; 
}

@keyframes slideIn {
    from {
        transform: translateY(-20px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

form {
    display: flex;
    flex-direction: column;
    padding: 3rem 1rem;
    margin: auto;
    max-width: 600px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
}

form label {
    color: #fff;
    margin-bottom: 0.5rem;
}

form input, form textarea {
    margin-bottom: 1rem;
    padding: 10px 18px;
    font-size: 1.2rem;
    background-color: rgba(255, 255, 255, 0.1);
    border: none;
    border-radius: 4px;
    color: #f4f4f4;
    transition: background-color 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease;
}

form input:focus, form textarea:focus {
    outline: none;
    background-color: rgba(255, 255, 255, 0.2);
    box-shadow: 0 0 8px rgba(255, 255, 255, 0.3);
}

form textarea {
    resize: vertical;
    min-height: 120px;
}

.submit {
    background: #7f5af0;
    color: #fff;
    border: none;
    padding: 12px 0;
    font-size: 1.2rem;
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.3s ease;
}

.submit:hover {
    background: #7f5af0;
}
@media (max-width: 600px) {
    .contact-heading h1 {
        font-size: 2.5rem;
    }
    .contact-heading p {
        font-size: 1.3rem;
    }
    form {
        padding: 1.5rem;
    }
    form input, form textarea {
        max-width: 100%;
    }
    .submit {
        max-width: 100%;
    }
}