.work-container {
    background-image: url("../Assets/bg3.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 2rem 0;
}

.project-heading {
    text-align: center;
    padding: 2rem 0;
    font-size: 2.5rem;
    color: #fff;
    opacity: 0;
    transform: translateY(-20px) scale(0.8);
    animation: headingAnimation 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    padding: 2rem;
    animation: fadeIn 1s ease-in-out;
}

.project-card {
    border: 2px solid grey;
    padding: 1.5rem;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    animation: slideIn 0.5s ease-in-out;
}

.project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.project-card img {
    width: 100%;
    border-radius: 8px;
    animation: fadeIn 2s ease-in-out;
}

.project-title {
    color: #fff;
    padding: 1rem 0;
    font-size: 1.5rem;
    text-align: center;
}

.project-details p {
    padding-bottom: 1rem;
    font-size: 1rem;
    color: #fff;
    text-align: justify;
}

.project-btns {
    display: flex;
    justify-content: space-between;
}

.btn {
    padding: 0.5rem 1rem;
    border-radius: 4px;
    text-decoration: none;
    color: #fff;
    transition: background 0.3s, transform 0.3s;
}

.view-btn {
    background: #28a745;
}

.view-btn:hover {
    background: #218838;
    transform: scale(1.05);
}

.source-btn {
    background: #007bff;
}

.source-btn:hover {
    background: #0056b3;
    transform: scale(1.05);
}

/* Keyframes for animations */
@keyframes headingAnimation {
    0% {
        opacity: 0;
        transform: translateY(-20px) scale(0.8);
    }
    60% {
        opacity: 1;
        transform: translateY(0) scale(1.1);
    }
    100% {
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}

@keyframes slideIn {
    from {
        transform: translateY(20px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* Responsive styles */
@media screen and (max-width: 740px) {
    .project-container {
        max-width: 90%;
        grid-template-columns: 1fr;
    }
}
