.about {
    background-image: url("../Assets/bg2.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3rem 1rem;
    min-height: 100vh;
}

.about-content {
    background: rgba(0, 0, 0, 0.6);
    border-radius: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 2rem;
    max-width: 1000px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    animation: fadeIn 1s ease-out;
    color: #fff;
}

.text-content {
    flex: 1;
    text-align: left;
    margin-right: 2rem;
    animation: slideInLeft 1s ease-out;
}

.heading {
    font-size: 3rem;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
}

.description {
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 1.5rem;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}

.button-container {
    display: flex;
    gap: 1rem;
}

.hero-btn {
    background-color: #7f5af0;
    padding: 12px 24px;
    border: none;
    color: #fff;
    font-size: 1.2rem;
    cursor: pointer;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s, transform 0.3s;
}

.hero-btn:hover {
    background-color: #826ebe;
    transform: translateY(-5px);
}

.img-container {
    flex: 1;
    display: flex;
    justify-content: center;
    animation: slideInRight 1s ease-out;
}

.img {
    max-width: 100%;
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideInLeft {
    from {
        opacity: 0;
        transform: translateX(-20px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes slideInRight {
    from {
        opacity: 0;
        transform: translateX(20px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@media screen and (max-width: 768px) {
    .about-content {
        flex-direction: column;
        text-align: center;
    }

    .text-content {
        margin-right: 0;
        margin-bottom: 2rem;
    }

    .heading {
        font-size: 2.5rem;
    }

    .description {
        font-size: 1rem;
    }

    .hero-btn {
        font-size: 1rem;
    }

    .img-container {
        justify-content: center;
    }
}

@media screen and (max-width: 480px) {
    .heading {
        font-size: 2rem;
    }

    .description {
        font-size: 0.9rem;
    }

    .hero-btn {
        font-size: 0.9rem;
        padding: 10px 20px;
    }
}
