body {
  margin: 0;
  padding: 0;
  background:#16161a;
  }
  
  .header {
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #11112f;
       }
                        
  .header-bg {
    background-color: rgba(139, 30, 139, 0.196);
     }
                          
 .header h1 {
    color: #fff;
    margin: 0;
            }
                            
.header a {
      text-decoration: none;
      color: white;
      
         }
                              
.nav-menu {
  display: flex;
  flex-direction: row; 
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  }
  .nav-menu :hover{
   transform: translateY(-10px);
   box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
                                
.nav-menu li {
   margin: 0 20px;
   }
           
.nav-menu a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
  }
                                    
.hamburger {
  display: none; 
  }
                                      
 @media (max-width: 768px) {
  .nav-menu {
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    height: 0; 
    overflow: hidden;
    transition: height 0.3s ease;
    }
                                          
.nav-menu.active {
  height: 100vh; 
  display: flex;
      }
                      
.nav-menu li {
  margin: 20px 0; 
}
                        
.hamburger {
  display: block; 
}
}
                      
                    