
.footer {
    width: 100%;
    padding: 6rem 0;
    background: #222; 
    color: #fff; 
}

.footer-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left {
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location {
    display: flex;
    align-items: center;
    margin-bottom: 1rem; 
}
.Phone {
    display: flex;
    align-items: center;
    margin-bottom: 1rem; 
}
.E-mail {
    display: flex;
    align-items: center;
    margin-bottom: 1rem; 
}

.right {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    margin-left: 300px;
}

.social {
    display: flex;
    margin-top: 1rem;
}

h4 {
    font-size: 1.4rem; 
    margin-bottom: 0.5rem;
}

p {
    font-size: 1.2rem;
    line-height: 1.5;
}

.social a {
    margin-right: 1rem; 
    transition: transform 0.3s ease; 
}

.social a:hover {
    transform: translateY(-3px); 
}

@media screen and (max-width: 640px) {
    .footer-container {
        grid-template-columns: 1fr;
    }
}
