body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;

    background-image: url("../Assets/bg.webp");
}

.resume-container {
    background-color: #11112f;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    margin: 0 auto;
    max-width: 800px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden; 
}

.download-button-container {
    margin-top: 20px;
}

.download-btn {
    background-color: #11112f;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: background-color 0.3s ease;
}

.download-btn:hover {
    background-color: #282c6b;
}

.download-button {
    color: white;
    text-decoration: none;
    display: flex;
    align-items: center;
}

a.download-button {
    display: flex;
    align-items: center;
    color: white;
    text-decoration: none;
}
